li {
  border-radius: 2px;
  background: #fafafa;
  box-shadow: 0px 5px 50px 0px rgba(32, 35, 41, 0.15);
  display: grid;
  grid-template-columns: 30% 50% 20%;
  align-items: center;
  justify-content: space-between;
  min-height: 100px;
  position: relative;
}
span {
  font-size: 24px;
  color: #716f6f;
}
.delete {
  position: absolute;
  cursor: pointer;
  right: 1rem;
  top: -1rem;
}
.delete circle{
  fill: #716f6f;
}
.delete:hover circle{
  fill: red;
}
.check{
    place-self: center;
    cursor: pointer;
}
.check--active {
  color: #4caf50;
}
.todo--completed{
  text-decoration: line-through;
}